import React, { useState, useEffect } from 'react';
import { useAuthState } from 'react-firebase-hooks/auth';
import {
  collection,
  addDoc,
  query,
  orderBy,
  limit,
  serverTimestamp,
  onSnapshot,
} from 'firebase/firestore';
import { auth, firestore } from './firebase'; // Adjust the path to your firebase.js file
import styles from './MessagingPage.module.css'; // Import CSS styles

const MessagingPage = () => {
  const [user] = useAuthState(auth);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [recipients, setRecipients] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedRecipient, setSelectedRecipient] = useState(null);
  const [conversationId, setConversationId] = useState(null); // State to hold the conversationId

  useEffect(() => {
    const unsubscribe = onSnapshot(collection(firestore, 'recipients'), (snapshot) => {
      const loadedRecipients = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      })).filter((recipient) => recipient.uid !== user?.uid); // Exclude the current user
      setRecipients(loadedRecipients);
    });
    return () => unsubscribe();
  }, [user]);

  useEffect(() => {
    if (user && selectedRecipient) {
      // Construct conversationId ensuring both users use the same identifier
      const participants = [user.uid, selectedRecipient.uid].sort().join('_');
      setConversationId(participants);

      const conversationRef = collection(firestore, 'conversations', participants, 'messages');
      const q = query(conversationRef, orderBy('createdAt'), limit(25));

      const unsubscribe = onSnapshot(q, (querySnapshot) => {
        const loadedMessages = [];
        querySnapshot.forEach((doc) => {
          loadedMessages.push(doc.data());
        });
        setMessages(loadedMessages);
      });

      return () => unsubscribe();
    }
  }, [user, selectedRecipient]); // Remove conversationId from dependencies

  const sendMessage = async (e) => {
    e.preventDefault();
    if (!selectedRecipient || newMessage.trim() === '') return;

    try {
      const participants = [user.uid, selectedRecipient.uid].sort().join('_');
      await addDoc(collection(firestore, 'conversations', participants, 'messages'), {
        text: newMessage,
        createdAt: serverTimestamp(),
        senderId: user.uid,
        displayName: user.displayName,
      });
      setNewMessage('');
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };

  const filteredRecipients = recipients.filter(recipient =>
    recipient.displayName.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className={styles.messagingPage}>
      <div className={styles.pageTitle}>Messaging Page</div>
      <div className={styles.messageContainer}>
        <div className={styles.recipientsList}>
          <h3>Recipients</h3>
          <input
            type="text"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            placeholder="Search recipients..."
            className={styles.searchInput}
          />
          <ul>
            {filteredRecipients.map((recipient) => (
              <li
                key={recipient.id}
                className={recipient.id === selectedRecipient?.id ? styles.selectedRecipient : ''}
                onClick={() => setSelectedRecipient(recipient)}
              >
                {recipient.displayName}
              </li>
            ))}
          </ul>
        </div>
        <div className={styles.conversation}>
          {conversationId ? (
            <>
              <h3>Conversation with {selectedRecipient?.displayName}</h3>
              <div className={styles.messageList}>
                {messages.map((message, index) => (
                  <div
                    key={index}
                    className={`${styles.message} ${
                      message.senderId === user.uid ? styles.sentMessage : styles.receivedMessage
                    }`}
                  >
                    <strong>{message.displayName}</strong>: {message.text}
                  </div>
                ))}
              </div>
              <form onSubmit={sendMessage} className={styles.messageForm}>
                <input
                  type="text"
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder={`Message ${selectedRecipient.displayName}...`}
                  className={styles.messageInput}
                />
                <button type="submit" className={styles.sendButton}>
                  Send
                </button>
              </form>
            </>
          ) : (
            <div className={styles.notificationBubble}>Select a recipient to start messaging.</div>
          )}
        </div>
      </div>
    </div>
  );
};

export default MessagingPage;
