import React from 'react';
import { GoogleAuthProvider, signInWithPopup } from 'firebase/auth';
import { doc, setDoc, getDoc, collection, getDocs, query, where, addDoc } from 'firebase/firestore';
import { useNavigate } from 'react-router-dom';
import { auth, firestore } from './firebase'; // Adjust the import path as per your setup
import styles from './SignIn.module.css'; // Import the updated CSS file
import GoogleLogo from './Google-Logo.png'; // Import Google logo image

const SignIn = () => {
  const navigate = useNavigate();

  const handleGoogleSignin = async () => {
    const provider = new GoogleAuthProvider();
    try {
      const { user } = await signInWithPopup(auth, provider);

      // Check if user document exists, if not, create one
      const userRef = doc(firestore, 'users', user.uid);
      const userSnapshot = await getDoc(userRef);

      let verifiedStatus = false; // Default value for 'verified' field
      if (userSnapshot.exists()) {
        // Retrieve 'verified' status from user document if it exists
        verifiedStatus = userSnapshot.data().verified;
      }

      // Initialize user document with name, email, and other details
      await setDoc(userRef, {
        name: user.displayName,
        email: user.email,
        verified: verifiedStatus,
        lastCheckIn: null,
        displayName: user.displayName, // Set displayName to user's name by default
      });

      // Check if user is already in recipients collection
      const recipientsRef = collection(firestore, 'recipients');
      const recipientsQuery = query(recipientsRef, where('uid', '==', user.uid));
      const recipientsSnapshot = await getDocs(recipientsQuery);

      if (recipientsSnapshot.empty) {
        // User is not in recipients collection, add them
        await addDoc(recipientsRef, {
          uid: user.uid,
          displayName: user.displayName,
        });
        console.log('User added to recipients:', user.displayName);
      } else {
        console.log('User already exists in recipients:', user.displayName);
      }

      // Redirect user after successful sign-in
      navigate('/dashboard');
    } catch (error) {
      console.error('Error signing in with Google:', error);
      // Handle error states or display error messages
    }
  };

  return (
    <div className={styles.signInContainer}>
      <div className={styles.notificationBanner}>
        <strong>For enhanced security and a seamless user experience, please sign in using Google.</strong>
      </div>
      <button onClick={handleGoogleSignin} className={styles.googleButton}>
        <img src={GoogleLogo} alt="Google Logo" className={styles.googleLogo} />
        <span className={styles.buttonText}>Sign In with Google</span>
      </button>
    </div>
  );
};

export default SignIn;
