import React, { useState, useEffect } from 'react';
import { updateDoc, addDoc } from 'firebase/firestore';
import styles from './CheckInForm.module.css';

const CheckInForm = ({ initialData, onSubmit, onCancel }) => {
  const [progress, setProgress] = useState('');
  const [challenges, setChallenges] = useState('');
  const [achievements, setAchievements] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    // Initialize form fields with initialData when editing
    if (initialData) {
      setProgress(initialData.progress || '');
      setChallenges(initialData.challenges || '');
      setAchievements(initialData.achievements || '');
    }
  }, [initialData]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      // Validate form fields
      if (!progress || !challenges || !achievements) {
        setError('Please fill in all fields.');
        setIsSubmitting(false);
        return;
      }

      const formData = {
        progress,
        challenges,
        achievements,
        id: initialData ? initialData.id : null,
      };

      // Execute onSubmit callback with form data
      if (onSubmit) {
        await onSubmit(formData);
      }

      // Reset form fields and close modal after successful submission
      setProgress('');
      setChallenges('');
      setAchievements('');
      setError(null);
    } catch (err) {
      console.error('Error handling form submission:', err);
      setError('Failed to submit form. Please try again later.'); // Set error notification
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => {
    // Reset form fields and cancel editing
    setProgress('');
    setChallenges('');
    setAchievements('');
    setError(null);

    if (onCancel) {
      onCancel();
    }
  };

  return (
    <form className={styles.checkInForm} onSubmit={handleSubmit}>
      <h3>{initialData ? 'Edit Check-in' : 'Submit Check-in'}</h3>
      {error && (
        <div className={styles.error}>
          <p>{error}</p>
          <button className={styles.closeButton} onClick={() => setError(null)}>X</button>
        </div>
      )}
      <div className={styles.formGroup}>
        <label className={styles.label}>Progress:</label>
        <textarea
          className={styles.textArea}
          value={progress}
          onChange={(e) => setProgress(e.target.value)}
          required
        />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.label}>Challenges:</label>
        <textarea
          className={styles.textArea}
          value={challenges}
          onChange={(e) => setChallenges(e.target.value)}
          required
        />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.label}>Achievements:</label>
        <textarea
          className={styles.textArea}
          value={achievements}
          onChange={(e) => setAchievements(e.target.value)}
          required
        />
      </div>
      <div className={styles.buttons}>
        <button type="submit" className={styles.submitButton} disabled={isSubmitting}>
          {initialData ? 'Save Changes' : 'Submit'}
        </button>
        {initialData && (
          <button type="button" className={styles.cancelButton} onClick={handleCancel}>
            Cancel
          </button>
        )}
      </div>
    </form>
  );
};

export default CheckInForm;
