import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { auth } from './firebase';
import SignIn from './SignIn';
import Home from './Home';
import Dashboard from './Dashboard';
import UserProfile from './UserProfile';
import VerifyEmail from './VerifyEmail';
import MessagingPage from './MessagingPage'; // Import the MessagingPage component
import Navbar from './Navbar';
import './App.css'; // Import global CSS

const App = () => {
  const [user, setUser] = useState(null);

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      setUser(user);
    });

    return () => unsubscribe();
  }, []);

  return (
    <Router>
      <div className="container">
        <Navbar user={user} />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/signin" element={<SignIn />} />
          <Route path="/dashboard" element={user ? <Dashboard user={user} /> : <Navigate to="/signin" />} />
          <Route path="/profile" element={user ? <UserProfile user={user} setUser={setUser} /> : <Navigate to="/signin" />} />
          <Route path="/verifyemail" element={<VerifyEmail />} />
          {/* Add a route for MessagingPage */}
          <Route path="/messages" element={user ? <MessagingPage /> : <Navigate to="/signin" />} />
        </Routes>
      </div>
    </Router>
  );
};

export default App;
