import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { auth, firestore } from './firebase';
import { doc, updateDoc, getDoc } from 'firebase/firestore';
import { getStorage, ref, uploadBytesResumable, getDownloadURL, deleteObject } from 'firebase/storage';
import styles from './UserProfile.module.css';

const UserProfile = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [displayName, setDisplayName] = useState('');
  const [photoURL, setPhotoURL] = useState('');
  const [newDisplayName, setNewDisplayName] = useState('');
  const [newPhotoFile, setNewPhotoFile] = useState(null);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploadingPhoto, setUploadingPhoto] = useState(false);
  const [editMode, setEditMode] = useState(false);
  const [isVerified, setIsVerified] = useState(false); // State to hold verification status
  const [interests, setInterests] = useState('');
  const [skills, setSkills] = useState('');
  const [goals, setGoals] = useState('');
  const [availability, setAvailability] = useState('');

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        setIsLoading(true);
        const userDocRef = doc(firestore, 'users', auth.currentUser.uid);
        const userDoc = await getDoc(userDocRef);
        if (userDoc.exists()) {
          const userData = userDoc.data();
          setDisplayName(userData.displayName || '');
          setPhotoURL(userData.photoURL || '');
          setNewDisplayName(userData.displayName || '');
          setInterests(userData.interests || '');
          setSkills(userData.skills || '');
          setGoals(userData.goals || '');
          setAvailability(userData.availability || '');

          // Check email verification status
          const currentUser = auth.currentUser;
          setIsVerified(currentUser.emailVerified);
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
      } finally {
        setIsLoading(false);
      }
    };

    const unsubscribe = auth.onAuthStateChanged((user) => {
      setUser(user);
      if (user) {
        fetchUserData();
      }
    });

    return () => unsubscribe();
  }, []);

  const handleSaveProfile = async () => {
    try {
      if (newPhotoFile) {
        setUploadingPhoto(true);
        const storage = getStorage();
        const userId = auth.currentUser.uid;
        const fileName = newPhotoFile.name;
        const storageRef = ref(storage, `user-profiles/${userId}/${fileName}`);
        const uploadTask = uploadBytesResumable(storageRef, newPhotoFile);

        uploadTask.on(
          'state_changed',
          (snapshot) => {
            const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
            setUploadProgress(progress);
          },
          (error) => {
            console.error('Error uploading photo:', error);
            setUploadingPhoto(false);
            setUploadProgress(0);
          },
          () => {
            getDownloadURL(uploadTask.snapshot.ref)
              .then((downloadURL) => {
                const updatedPhotoURL = downloadURL;

                // Update user document with new display name, photo URL, and other fields
                const userDocRef = doc(firestore, 'users', userId);
                updateDoc(userDocRef, {
                  displayName: newDisplayName,
                  photoURL: updatedPhotoURL,
                  interests,
                  skills,
                  goals,
                  availability,
                })
                  .then(() => {
                    setDisplayName(newDisplayName);
                    setPhotoURL(updatedPhotoURL);
                    setNewPhotoFile(null);
                    setUploadingPhoto(false);
                    setUploadProgress(0);
                    setEditMode(false); // Exit edit mode after save
                  })
                  .catch((error) => {
                    console.error('Error updating user document:', error);
                    setUploadingPhoto(false);
                    setUploadProgress(0);
                  });
              })
              .catch((error) => {
                console.error('Error fetching download URL:', error);
                setUploadingPhoto(false);
                setUploadProgress(0);
              });
          }
        );
      } else {
        // Update user document with new display name and other fields (no photo change)
        const userDocRef = doc(firestore, 'users', auth.currentUser.uid);
        await updateDoc(userDocRef, {
          displayName: newDisplayName,
          interests,
          skills,
          goals,
          availability,
        });
        setDisplayName(newDisplayName);
        setEditMode(false); // Exit edit mode after save
      }
    } catch (error) {
      console.error('Error saving profile:', error);
    }
  };

  const handlePhotoChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setNewPhotoFile(file);
      const reader = new FileReader();
      reader.onload = () => {
        setPhotoURL(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleDisplayNameChange = (e) => {
    setNewDisplayName(e.target.value);
  };

  const handleInterestsChange = (e) => {
    setInterests(e.target.value);
  };

  const handleSkillsChange = (e) => {
    setSkills(e.target.value);
  };

  const handleGoalsChange = (e) => {
    setGoals(e.target.value);
  };

  const handleAvailabilityChange = (e) => {
    setAvailability(e.target.value);
  };

  const handleEdit = () => {
    setEditMode(true); // Enable edit mode
  };

  const handleCancel = () => {
    // Reset form fields to current values
    setNewPhotoFile(null);
    setNewDisplayName(displayName);
    setPhotoURL(photoURL);
    setUploadProgress(0);
    setEditMode(false); // Exit edit mode
  };

  const deleteOldProfilePicture = (photoURL) => {
    if (!photoURL) {
      return; // No photo to delete
    }

    const storage = getStorage();
    const oldPhotoRef = ref(storage, photoURL);

    deleteObject(oldPhotoRef)
      .then(() => {
        console.log('Old profile picture deleted successfully');
      })
      .catch((error) => {
        console.error('Error deleting old profile picture:', error);
      });
  };

  const handleDeletePhoto = () => {
    if (!photoURL) {
      console.log('No photo to delete.');
      return;
    }

    deleteOldProfilePicture(photoURL);

    // Update photoURL to blank in the database
    const userId = auth.currentUser.uid;
    const userDocRef = doc(firestore, 'users', userId);
    updateDoc(userDocRef, { photoURL: '' })
      .then(() => {
        console.log('Photo URL cleared successfully');
        setPhotoURL(''); // Clear photo URL from state
      })
      .catch((error) => {
        console.error('Error clearing photo URL:', error);
      });
  };

  if (!user) {
    return <div>Loading...</div>;
  }

  return (
    <div className={styles.userProfile}>
      <h2>User Profile</h2>
      <div className={styles.profileInfo}>
        {photoURL ? (
          <img className={styles.profileImage} src={photoURL} alt="User Profile" />
        ) : (
          <div className={styles.noProfileImage}>No Photo</div>
        )}
        {editMode && !photoURL && (
          <label className={styles.fileInputLabel}>
            Choose File
            <input
              type="file"
              accept="image/*"
              onChange={handlePhotoChange}
              className={styles.fileInput}
            />
          </label>
        )}
        {uploadingPhoto && (
          <div className={styles.uploadProgress}>
            Uploading... {uploadProgress}%
          </div>
        )}
        <input
          type="text"
          value={newDisplayName}
          onChange={handleDisplayNameChange}
          className={styles.profileInput}
          placeholder="Display Name"
          disabled={!editMode}
        />
        <textarea
          value={interests}
          onChange={handleInterestsChange}
          className={styles.profileTextarea}
          placeholder="Interests"
          disabled={!editMode}
        />
        <textarea
          value={skills}
          onChange={handleSkillsChange}
          className={styles.profileTextarea}
          placeholder="Skills"
          disabled={!editMode}
        />
        <textarea
          value={goals}
          onChange={handleGoalsChange}
          className={styles.profileTextarea}
          placeholder="Goals"
          disabled={!editMode}
        />
        <textarea
          value={availability}
          onChange={handleAvailabilityChange}
          className={styles.profileTextarea}
          placeholder="Availability"
          disabled={!editMode}
        />
        {editMode ? (
          <div className={styles.profileActions}>
            <button onClick={handleSaveProfile} className={styles.saveButton}>
              Save
            </button>
            <button onClick={handleCancel} className={styles.cancelButton}>
              Cancel
            </button>
            {photoURL && (
              <button onClick={handleDeletePhoto} className={styles.deleteButton}>
                Delete Photo
              </button>
            )}
          </div>
        ) : (
          <div className={styles.profileActions}>
            <button onClick={handleEdit} className={styles.editButton}>
              Edit Profile
            </button>
          </div>
        )}
        {/* Display verification status as a badge */}
        {isVerified ? (
          <span className={styles.verifiedBadge}>Verified</span>
        ) : (
          <span className={styles.notVerifiedBadge}>Not Verified</span>
        )}
      </div>
    </div>
  );
};

export default UserProfile;
