// VerifyEmail.js

import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { auth } from './firebase'; // Adjust imports as per your project structure
import { sendEmailVerification, reload } from 'firebase/auth'; // Import necessary Firebase auth functions
import styles from './VerifyEmail.module.css'; // Adjust CSS import as per your project structure

const VerifyEmail = () => {
  const navigate = useNavigate();
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    const user = auth.currentUser;
    if (user && !user.emailVerified) {
      sendVerificationEmail(user);
    } else {
      navigate('/dashboard'); // Redirect to dashboard if email is already verified
    }
  }, [navigate]);

  const sendVerificationEmail = async (user) => {
    try {
      setIsLoading(true);
      await sendEmailVerification(user);
      setIsLoading(false);
      setError('Verification email sent. Please check your inbox.');
    } catch (error) {
      setError('Failed to send verification email. Please try again later.');
      setIsLoading(false);
    }
  };

  const handleResendVerification = async () => {
    try {
      setIsLoading(true);
      const user = auth.currentUser;
      if (user) {
        await sendEmailVerification(user);
        setIsLoading(false);
        setError('Verification email sent. Please check your inbox.');
      } else {
        throw new Error('User not found.');
      }
    } catch (error) {
      setError(error.message);
      setIsLoading(false);
    }
  };

  return (
    <div className={styles.verifyEmailContainer}>
      <h2>Verify Your Email</h2>
      <p className={styles.error}>{error}</p>
      <p>A verification email has been sent to your inbox. Please verify your email before proceeding.</p>
      <button className={styles.verifyEmailButton} onClick={handleResendVerification} disabled={isLoading}>
        {isLoading ? 'Resending...' : 'Resend Verification Email'}
      </button>
    </div>
  );
};

export default VerifyEmail;
