import { initializeApp } from 'firebase/app';
import { getAuth, browserLocalPersistence, setPersistence, GoogleAuthProvider } from 'firebase/auth';
import { getFirestore, collection, doc, setDoc, getDoc, deleteDoc, updateDoc, addDoc } from 'firebase/firestore'; // Import Firestore functions
import {query, orderBy, limit, onSnapshot, serverTimestamp } from 'firebase/firestore';

const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
  measurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Firebase services
export const auth = getAuth(app);
export const firestore = getFirestore(app); // Export Firestore instance

// Google Auth Provider
export const googleProvider = new GoogleAuthProvider();

// Enable persistence (optional but recommended)
setPersistence(auth, browserLocalPersistence)
  .then(() => {
    // Persistence enabled
  })
  .catch((error) => {
    console.error('Error setting persistence:', error);
  });

export {serverTimestamp, collection, addDoc, query, orderBy, limit, onSnapshot };
