import React, { useState, useEffect } from 'react';
import { auth, firestore } from './firebase'; // Adjust the path as per your project structure
import { collection, getDoc, getDocs, setDoc, doc, deleteDoc, updateDoc, addDoc } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import styles from './Dashboard.module.css';
import CheckInForm from './CheckInForm';
import UserProfile from './UserProfile';

const Dashboard = () => {
  const [checkIns, setCheckIns] = useState([]);
  const [editingCheckIn, setEditingCheckIn] = useState(null);
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true); // Loading indicator state
  const [isAddingCheckIn, setIsAddingCheckIn] = useState(false); // State to manage adding new check-in
  const [notification, setNotification] = useState(null); // Notification state for errors
  const navigate = useNavigate(); // Initialize useNavigate hook for navigation

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (user) {
        setUser(user);

        // Check if user document exists, if not, create one
        const userRef = doc(firestore, 'users', user.uid);
        const docSnapshot = await getDoc(userRef);

        if (!docSnapshot.exists()) {
          await setDoc(userRef, {
            name: user.displayName,
            email: user.email,
            verified: false, // Initialize 'verified' to false
            lastCheckIn: null,
          });
        }

        if (!user.emailVerified) {
          navigate('/verifyemail'); // Redirect to /verifyemail if email is not verified
        } else {
          fetchCheckIns(user.uid); // Fetch check-ins for the current user
        }
      } else {
        setUser(null);
        navigate('/signin'); // Redirect to sign-in if not authenticated
      }
    });

    return () => unsubscribe();
  }, [navigate]);

  useEffect(() => {
    // Fetch check-ins when user changes
    if (user && user.emailVerified) {
      fetchCheckIns(user.uid);
    }
  }, [user]);

  const fetchCheckIns = async (userId) => {
    try {
      setIsLoading(true); // Set loading state while fetching
      const querySnapshot = await getDocs(collection(firestore, 'checkIns'));
      const userCheckIns = querySnapshot.docs
        .map((doc) => ({ id: doc.id, ...doc.data() }))
        .filter((checkIn) => checkIn.userId === userId)
        .map((checkIn) => ({
          ...checkIn,
          createdAt: checkIn.createdAt.toDate(), // Convert Firestore Timestamp to Date
        }));
      setCheckIns(userCheckIns);
    } catch (error) {
      console.error('Error fetching check-ins:', error);
    } finally {
      setIsLoading(false); // Reset loading state after fetching
    }
  };

  const handleEditCheckIn = (checkInId) => {
    const checkInToEdit = checkIns.find((checkIn) => checkIn.id === checkInId);
    if (checkInToEdit && !checkInToEdit.reviewed) {
      setEditingCheckIn(checkInToEdit);
      setIsAddingCheckIn(false); // Ensure add check-in form is not shown when editing
    } else {
      console.error('Cannot edit this check-in.');
    }
  };

  const handleDelete = async (checkInId) => {
    try {
      const checkInRef = doc(firestore, 'checkIns', checkInId);
      await deleteDoc(checkInRef);

      // Refetch check-ins after deletion
      fetchCheckIns(user.uid);
    } catch (error) {
      console.error('Error deleting check-in:', error);
    }
  };

  const handleCancelEditCheckIn = () => {
    setEditingCheckIn(null); // Reset editing state after cancellation
  };

  const handleSubmitCheckInForm = async (formData) => {
    try {
      if (!formData) {
        console.error('Form data is missing.');
        return;
      }

      const { id, ...updatedData } = formData;

      // Check if there are recent check-ins within the last month
      const lastMonth = new Date();
      lastMonth.setMonth(lastMonth.getMonth() - 1);

      const recentCheckIn = checkIns.find((checkIn) => checkIn.createdAt > lastMonth);

      if (id) {
        // Editing an existing check-in
        const checkInRef = doc(firestore, 'checkIns', id);
        await updateDoc(checkInRef, updatedData);
        showSuccessNotification('Check-in updated successfully!');
      } else if (recentCheckIn) {
        // Trying to submit a new check-in within last month
        setNotification('Cannot submit new check-in. Last check-in was within the last month.');
        return;
      } else {
        // Adding a new check-in
        const newCheckIn = {
          userId: auth.currentUser.uid,
          ...updatedData,
          reviewed: false,
          createdAt: new Date(),
        };
        const checkInsCollection = collection(firestore, 'checkIns');
        await addDoc(checkInsCollection, newCheckIn);

        // Update user's lastCheckIn date
        const userRef = doc(firestore, 'users', auth.currentUser.uid);
        await updateDoc(userRef, {
          lastCheckIn: new Date(),
        });

        showSuccessNotification('Check-in submitted successfully!');
      }

      // Refetch check-ins to update the list
      fetchCheckIns(user.uid);
    } catch (error) {
      console.error('Error handling form submission:', error);
      setNotification('Failed to submit form. Please try again later.'); // Set error notification
    } finally {
      setEditingCheckIn(null); // Hide edit form after submission
      setIsAddingCheckIn(false); // Hide add check-in form after submission
    }
  };

  const showSuccessNotification = (message) => {
    setNotification(message);
    setTimeout(() => {
      setNotification(null);
    }, 3000); // Clear notification after 3 seconds
  };

  const handleAddCheckIn = () => {
    setIsAddingCheckIn(true); // Show add check-in form under user profile
    setEditingCheckIn(null); // Ensure editing check-in form is hidden
  };

  const handleNotificationClose = () => {
    setNotification(null); // Clear notification
  };

  if (!user || !user.emailVerified) return null; // Handle case where user state is not yet initialized or email is not verified

  return (
    <div className={styles.dashboardContainer}>
      <UserProfile user={user} setUser={setUser} />
      <div className={styles.actions}>
        <button className={styles.addButton} onClick={handleAddCheckIn}>
          Add Check-in
        </button>
      </div>
      {notification && (
        <div className={styles.errorContainer}>
          <p>{notification}</p>
          <button className={styles.closeButton} onClick={handleNotificationClose}>X</button>
        </div>
      )}
      <div className={styles.checkInList}>
        {checkIns.length > 0 ? (
          checkIns.map((checkIn) => (
            <div key={checkIn.id} className={styles.checkIn}>
              <h3>Date: {checkIn.createdAt.toLocaleString()}</h3>
              <p>Progress: {checkIn.progress}</p>
              <p>Challenges: {checkIn.challenges}</p>
              <p>Achievements: {checkIn.achievements}</p>
              {!checkIn.reviewed && (
                <div className={styles.actions}>
                  <button onClick={() => handleEditCheckIn(checkIn.id)}>Edit</button>
                  <button onClick={() => handleDelete(checkIn.id)}>Delete</button>
                </div>
              )}
            </div>
          ))
        ) : (
          <p>No check-ins found.</p>
        )}
      </div>
      {isAddingCheckIn && (
        <div className={styles.modalBackground}>
          <CheckInForm onSubmit={handleSubmitCheckInForm} onCancel={handleCancelEditCheckIn} />
        </div>
      )}
      {editingCheckIn !== null && (
        <div className={styles.modalBackground}>
          <CheckInForm
            initialData={editingCheckIn}
            onSubmit={handleSubmitCheckInForm}
            onCancel={handleCancelEditCheckIn}
          />
        </div>
      )}
    </div>
  );
};

export default Dashboard;
