import React from 'react';
import styles from './Home.module.css';
import logo from './GenConnect_Logo.png'; // Adjust the path to your logo image

const Home = () => {
  return (
    <div className={styles.homeContainer}>
      <header className={styles.header}>
        <div className={styles.logoContainer}>
          <img src={logo} alt="GenConnect Logo" className={styles.logo} />
        </div>
        <div className={styles.titleContainer}>
          <h1 className={styles.title}>GenConnect</h1>
          <p className={styles.subtitle}>Empowering Youth through Mentorship</p>
        </div>
      </header>

      <section className={`${styles.section} ${styles.parallax}`}>
        <div className={styles.sectionContent}>
          <h2 className={styles.sectionTitle}>Project Overview</h2>
          <p>
            GenConnect aims to empower youth through mentorship, focusing on academic, personal, and social development.
          </p>
        </div>
      </section>

      <section className={`${styles.section} ${styles.parallax}`}>
        <div className={styles.sectionContent}>
          <h2 className={styles.sectionTitle}>Objectives</h2>
          <ul className={styles.sectionList}>
            <li>Enhance academic performance and attendance.</li>
            <li>Promote positive attitudes and reduce risky behaviors.</li>
            <li>Facilitate meaningful mentorship relationships.</li>
          </ul>
        </div>
      </section>

      <section className={`${styles.section} ${styles.parallax}`}>
        <div className={styles.sectionContent}>
          <h2 className={styles.sectionTitle}>Target Community</h2>
          <p>
            Local youth aged 15-19 seeking guidance for personal and professional development.
          </p>
        </div>
      </section>

      <section className={`${styles.section} ${styles.parallax}`}>
        <div className={styles.sectionContent}>
          <h2 className={styles.sectionTitle}>Activities</h2>
          <ul className={styles.sectionList}>
            <li>Conduct mentor-mentee pairing workshops and skill development sessions.</li>
            <li>Organize regular check-ins and community outreach events.</li>
          </ul>
        </div>
      </section>

      <section className={`${styles.section} ${styles.parallax}`}>
        <div className={styles.sectionContent}>
          <h2 className={styles.sectionTitle}>Methodology</h2>
          <p>
            Utilizes mentorship training to build positive, lasting relationships between mentors and mentees.
          </p>
        </div>
      </section>

      <section className={`${styles.section} ${styles.parallax}`}>
        <div className={styles.sectionContent}>
          <h2 className={styles.sectionTitle}>Expected Outcomes</h2>
          <ul className={styles.sectionList}>
            <li>Achieve improved academic performance and reduced risky behaviors.</li>
            <li>Promote enhanced self-esteem and positive social-emotional development.</li>
          </ul>
        </div>
      </section>

      <section className={`${styles.section} ${styles.parallax}`}>
        <div className={styles.sectionContent}>
          <h2 className={styles.sectionTitle}>Challenges and Solutions</h2>
          <ul className={styles.sectionList}>
            <li>Ensure mentor-mentee compatibility through comprehensive training.</li>
            <li>Maintain long-term engagement through ongoing support and training.</li>
            <li>Build community trust and engagement via strategic partnerships.</li>
          </ul>
        </div>
      </section>
    </div>
  );
};

export default Home;
